import { getInfo, setInfo } from '@/utils/storage'

export default {
  namespaced: true,
  state() {
    return {
      userInfo: getInfo(),
    }
  },
  mutations: {
    setUserInfo(state, obj) {
      console.log('a', state.userInfo)
      state.userInfo = obj
      setInfo(obj)
      console.log('b', state.userInfo)
    },
  },
  actions: {
    logout(context) {
      context.commit('setUserInfo', {})
      context.commit('cart/setCartList', [], { root: true })
    },
  },
  getters: {
    token: (state) => {
      return state.userInfo.token
    },
  },
}
